import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col, message } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getDisciplines, deleteDiscipline } from 'services/Discipline';

class ListTypeDisciplinesPage extends Component {
  state = { reload: 0 };
  handleDelete = async id => {
    await deleteDiscipline(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Loại xử lý',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Mức xử lý',
        dataIndex: 'parentLabel',
        key: 'parentLabel',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#typeDisciplines/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Xử lý kỷ luật">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#/typeDisciplines/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData reload={reload} service={getDisciplines} columns={columns} rowKey="id" />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypeDisciplinesPage />
  </Page>
);



